<template>
  <div class="searchlists">
    <h6>最佳匹配</h6>
    <div>
      <div class="aritis" v-if="seachmv.artist">
        <img :src="seachmv.artist[0].picUrl" alt />
        <div>歌手：{{seachmv.artist[0].name}}</div>
        <i></i>
      </div>
      <div class="mv" v-if="seachmv.mv">
        <img :src="seachmv.mv[0].cover" alt />
        <div>
          <h5>MV：{{seachmv.mv[0].name}}</h5>
          <p>{{seachmv.mv[0].artistName}}</p>
        </div>
        <i></i>
      </div>
      <div class="mv" v-if="seachmv.album">
        <img :src="seachmv.album[0].picUrl" alt />
        <div>
          <h5>专辑：{{seachmv.album[0].name}}</h5>
          <p>{{seachmv.album[0].artist.name}}</p>
        </div>
        <i></i>
      </div>
    </div>
    <ul class="getseach" v-if="seachlist">
      <SongList
        v-for="(item,index) in seachlist.songs"
        :key="index"
        :item="item"
        :is-show-quality="true"
        @translate-currentid="$emit('translate-currentid', [$event,seachlist.songs])"
      ></SongList>
    </ul>
  </div>
</template>

<script>
import SongList from "@/components/SongList.vue";
export default {
  components: {
    SongList
  },
  props: {
    names: String
  },
  data() {
    return {
      seachlist: {},
      seachmv: {}
    };
  },
  watch: {
    names: function() {
      if (this.names) {
        this.axios
          .get("https://music.kele8.cn/search?keywords=" + this.names)
          .then(repson => {
            this.seachlist = repson.data.result;
          });
      }
      if (this.names) {
        this.axios
          .get("https://music.kele8.cn/search/multimatch?keywords=" + this.names)
          .then(repson => {
            this.seachmv = repson.data.result;
          });
      }
    }
  }
};
</script>

