<template>
  <div class="playlis">
    <div v-if="playlist">
      <ListTitle :item="[name,url]" :user="playlist.creator"></ListTitle>
      <SongLabel :item="playlist"></SongLabel>
    </div>
    <div class="list">歌曲列表</div>
    <ul>
      <template v-if="playlist">
        <SongList v-for="(item,index) in playlist.tracks" :key="index" :item="item" :index="index" :is-show-quality="false" @translate-currentid="$emit('translate-currentid',[$event,playlist.tracks])"></SongList>
      </template>
    </ul>
  </div>
</template>

<script>

import ListTitle from "../components/ListTitle.vue";
import SongList from "../components/SongList.vue";
import SongLabel from "../components/SongLabel.vue";
export default {
  components: {
    SongList,
    ListTitle,
    SongLabel
  },
  data() {
    return {
      playlist: null,
      name:null,
      url:null,
    };
  },
  methods: {
    getliist(id,name,url) {
      this.axios
        .get("https://music.kele8.cn/playlist/detail", {
          params: {
            id: id
          }
        })
        .then(repson => {
          this.playlist = repson.data.playlist;
        });
        this.name=name;
        this.url=url;
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getliist(to.query.id,to.query.name,to.query.url);   
    });
  }
};
</script>

<style lang="less">
body,
html {
  height: 100%;
}
.playlis {
  height: calc(100% - 50px);
  overflow: scroll;
  .list{
    background: rgb(238, 238, 238);
    font-size: 12px;
    padding: 3px 6px;
    color: rgb(99, 98, 98);
  }
  ul{
    background: rgb(247, 247, 247);
  }
}
</style>