<template>
  <li class="songlist" @click="setCurrentSongId(item.id)">
    <div :class="{left:true,rednum:num<4&&hotsong}" v-if="index !== undefined">{{num}}</div>
    
    <dl>
      <dt>{{item.name}}</dt>
      <template v-if="item.song">
        <dd>
          <span class="songsq" v-if="(item.song.hMusic) && isShowQuality"></span>
          {{item.song.artists[0].name}}-{{item.song.album.name}}
        </dd>
      </template>
      <template v-else-if="item.artists">
        <dd>
          <span class="songsq" v-if="(item.hMusic) && isShowQuality"></span>
          {{item.artists[0].name}}-{{item.album.name}}
        </dd>
      </template>
      <template v-else>
        <dd>
          <span class="songsq" v-if="(item.h) && isShowQuality"></span>
          {{item.ar[0].name}}-{{item.al.name}}
        </dd>
      </template>
    </dl>
    <div class="right"></div>
  </li>
</template>

<script>
export default {
  props: ["item", "index", "isShowQuality", "hotsong"],
  data() {
    return {
      num: 0
    };
  },
  methods: {
    setCurrentSongId(id) {
      this.$emit("translate-currentid", id);
    },
    hotsongnum() {
      if (this.index !== undefined) {
        this.num = this.index + 1;
      }
      if (this.index !== undefined && this.hotsong == true && this.num < 10) {
        this.num = "0" + (this.index + 1);
      }
    }
  },
  created() {
    this.hotsongnum();
  }
};
</script>

<style lang="less">
.songlist {
  border-bottom: 1px solid rgb(239, 239, 239);
  width: 100%;
  padding: 6px 0;
  display: flex;
  
  .left {
    flex: 1;
    text-align: center;
    color: rgb(156, 156, 156);
    margin: auto;
  }
  .rednum {
    color: #dc0000;
  }
  .right {
    flex: 1;
    text-align: center;
    background: url(//s3.music.126.net/mobile-new/img/index_icon_3x.png?49a82fee3e0fc602e9c8b25df2bda159=);
    background-repeat: no-repeat;
    background-position: -16px 8px;
    background-size: 166px 97px;
  }
  dl {
    flex: 7;
    dd {
      font-size: 12px;
      color: rgb(148, 148, 148);
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      .songsq {
        display: inline-block;
        width: 12px;
        height: 8px;
        margin-right: 4px;
        background: url(//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=);
        background-size: 166px 97px;
      }
    }
    dt {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}
</style>