<template>
  <div class="songsearchlist">
    <div v-show="xxx">
      <h2 v-if="songname!=''&&songname!=null">搜索“{{songname}}”</h2>
      <hr />
      <ul class="search" v-if="seachlist&&songname!=''&&songname!=null">
        <li
          class="names"
          v-for="(item,index) in seachlist.allMatch"
          :key="index"
          @click="changeget(item.keyword)"
        >
          <img
            src="data:image/svg+xml;base64,"
            class="searchglass"
          />
          <div>{{item.keyword}}</div>
        </li>
      </ul>
    </div>
    <div v-show="xxx == false&&songname!=null&&songname!=''">
      <SearchList :names="songname" @translate-currentid="$emit('translate-currentid', $event)"></SearchList>
    </div>
  </div>
</template>

<script>
import SearchList from "@/components/SearchList.vue";
export default {
  components: {
    SearchList
  },
  data() {
    return {
      seachlist: null,
    };
  },
  props: ["songname", "xxx","enter"],
  methods: {
    changeget(name) {
      this.$emit("changexxx",name);
    }
  },
  watch: {
    songname: function() {
      if (this.songname) {
        this.axios
          .get(
            "https://music.kele8.cn/search/suggest?keywords=" +
              this.songname +
              "&type=mobile"
          )
          .then(repson => {
            this.seachlist = repson.data.result;
          });
      }
    }
  }
};
</script>

<style lang="less">
.songsearchlist {
  h2 {
    color: rgb(61, 122, 180);
    font-size: 14px;
  }
  .search {
    li.names {
      display: flex;
      line-height: 40px;
      border-bottom: 1px solid rgb(245, 245, 245);
      img.searchglass {
        flex: 1;
        width: 15px;
        height: 15px;
        margin: auto;
      }
      div {
        flex: 8;
        color: rgb(48, 48, 48);
      }
    }
  }
}
</style>