<template>
  <div class="recommend">
    <HomeTitle>推荐歌单</HomeTitle>
    <ul>
      <SongCard v-for="(item,index) in personalized" :key="index" :item="item"></SongCard>
    </ul>

    <HomeTitle>最新音乐</HomeTitle>
    <ul class="newul">
      <SongList
        v-for="(item,index) in newsong"
        :key="index"
        :item="item"
        :is-show-quality="true"
        @translate-currentid="$emit('translate-currentid', [$event,newsong])"
      ></SongList>
    </ul>
    <div class="recommendtail">
      <div class="ih">
        <i></i>
        <h1>网易云音乐</h1>
      </div>
      <h4>打开APP,发现更多好音乐></h4>
      <p>网易公司版权所有©1997-2019 杭州乐读科技有限公司运营</p>
    </div>
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";
import SongCard from "@/components/SongCard.vue";
import SongList from "@/components/SongList.vue";
export default {
  name: "HomeRecommend",
  components: {
    HomeTitle,
    SongCard,
    SongList
  },
  data: function() {
    return {
      personalized: [],
      newsong: []
    };
  },
  methods: {
    getpersonalized() {
      this.axios
        .get("https://music.kele8.cn/personalized?limit=6")
        .then(response => {
          this.personalized = response.data.result;
        });
    },
    getNewsong() {
      this.axios
        .get("https://music.kele8.cn/personalized/newsong")
        .then(response => {
          ({ result: this.newsong } = response.data);
        });
    }
  },
  created() {
    this.getpersonalized();
    this.getNewsong();
  }
};
</script>

