<template>
  <div class="playmusic">
    <div :class="{box:true,active:upmusic}" v-if="nowmusic">
      <img :src="nowmusic.al.picUrl" @click="getmusichmi()" />
      <h1 class="nowmusich1">{{nowmusic.name}} - {{nowmusic.ar[0].name}}</h1>
      <div class="stop" @click="$emit('musicstart')"></div>
    </div>
    <div class="box" v-else>
      <img src="../assets/logo.png" alt />
      <h1 class="nowmusich1">网易云 - 因有你更精彩</h1>
    </div>
  </div>
</template>

<script>
export default {
  props: ["musicid", "onoff", "upmusic"],
  data() {
    return {
      nowmusic: null
    };
  },
  methods: {
    getmusichmi() {
      this.$emit("showchange", true);
      this.$router.push({ path: "playerinterface" ,query:{id:this.musicid}});
    }
  },
  watch: {
    musicid: function() {
      this.axios
        .get("https://music.kele8.cn/song/detail?ids=" + this.musicid)
        .then(repson => {
          this.nowmusic = repson.data.songs[0];
        });
    },
    onoff: function() {
      if (document.querySelector(".stop").style) {
        if (this.onoff == true) {
          document.querySelector(".stop").style.backgroundPositionX = "-32px";
        } else {
          document.querySelector(".stop").style.backgroundPositionX = "1px";
        }
      }
    }
  }
};
</script>

<style lang="less">
.playmusic {
  height: 50px;

  .box {
    position: fixed;
    bottom: 0px;
    left: 0;
    width: 100%;
    height: 50px;
    background: rgba(250, 250, 250);
    border-top: 1px solid rgb(231, 231, 231);
    display: flex;
    align-items: center;
    img {
      height: 40px;
      width: 40px;
      border-radius: 50%;
      margin: 0 15px;
    }
    .nowmusich1 {
      flex: 1;
      line-height: 30px;
      font-size: 14px;
    }
    .stop {
      width: 30px;
      height: 30px;
      background: url("../assets/stop.jpg");
      background-repeat: no-repeat;
      background-size: 60px 30px;
      background-position-x: -32px;
      margin-right: 15px;
    }
  }
  .active {
    bottom: -50px;
  }
}
</style>