<template>
  <div class="homehot">
    <div class="hotsonglist">
      <div class="hotimg">
        <div class="hontsong"></div>
        <span>更新日期：</span>
        <span>{{day}}</span>
      </div>
      <div class="blackhotimg"></div>
    </div>
    <ul class="hotsongul">
      <SongList
        v-for="(item,index) in (hotsong.tracks || '').slice(0,num)"
        :key="index"
        :item="item"
        :index="index"
        :hotsong="true"
        :is-show-quality="true"
        @translate-currentid="$emit('translate-currentid', [$event,hotsong.tracks])"
      ></SongList>
    </ul>
    <div class="morelist" @click="num=200">查看完整榜单></div>
  </div>
</template>

<script>
import SongList from "@/components/SongList.vue";
export default {
  components: {
    SongList
  },
  data() {
    return {
      hotsong: {},
      num: 20,
      daynum: null,
      day: null
    };
  },
  methods: {
    getHotsong() {
      this.axios
        .get("https://music.kele8.cn/top/list?idx=1")
        .then(response => {
          this.hotsong = response.data.playlist;
          this.daynum = this.hotsong.updateTime;
          var d = new Date(this.daynum);
          this.day = d.getMonth() + 1 + "月" + d.getDate() + "日";
        });
    }
  },
  created() {
    this.getHotsong();
  }
};
</script>

<style lang="less">
.homehot{
  height: calc(100% - 179px);
  overflow: scroll;
}
.hotsongul {
  background: rgb(252, 252, 252);
}
.morelist {
  font-size: 14px;
  width: 100%;
  line-height: 55px;
  text-align: center;
  color: rgba(0, 0, 0, 0.418);
  background: rgb(252, 252, 252);
}
.hotsonglist {
  background: url("//s3.music.126.net/mobile-new/img/hot_music_bg_3x.jpg?2bfefd3ba37eaffbd66b7d108b9de26a=");
  background-size: cover;
  padding: 25px 20px 25px 20px;
  position: relative;

  .blackhotimg {
    display: block;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.19);
    position: absolute;
    top: 0;
    left: 0;
  }
  .hotimg {
    position: relative;
    z-index: 2;
    .hontsong {
      display: block;
      height: 67px;
      width: 142px;
      background-image: url("//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
      background-repeat: no-repeat;
      background-size: 166px 97px;
      background-position: -24px -30px;
      margin-bottom: 10px;
    }
    span {
      font-size: 10px;
      color: rgba(250, 250, 250, 0.8);
    }
  }
}
</style>