        v-for="(item, index) in lists"
        :key="index"
        @click="$emit('translate-currentid',item.id)"
      >{{item.name}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["lists", "name"]
};
</script>

<style lang="less">
.musiclist {
  position: absolute;
  width: 100%;
  bottom: 0;
  background: rgba(46, 46, 46, 0.938);
  padding: 10px;
  border-radius: 12px;
  max-height: 350px;
  overflow: scroll;
  .listul {
    width: 100%;
    .lists {
      color: #fff;
      line-height: 40px;
      width: 100%;
      text-align: center;
    }
    .namecolor {
      color: #dc0000;
    }
  }
}
</style>