<template>
  <li class="card" @click="getlist(item.id,item.picUrl,item.name)">
    <div class="laji">
      <img :src="item.picUrl" alt />
      <span><i></i>{{getplayCount(item.playCount)}}</span>
    </div>
    <p>{{item.name}}</p>
  </li>
</template>

<script>
export default {
  props: { item: Object },
  methods: {
    getplayCount(x) {
      if (x > 99999999) {
        return (x / 100000000).toFixed(2) + "亿";
      } else if (x > 99999) {
        return (x / 10000).toFixed(1) + "万";
      } else {
        return x;
      }
    },
    getlist(x,y,z){
      this.$router.push({path:"playlist",query:{id:x,url:y,name:z}});
    }
  }
};
</script>

<style lang="less">
.card {
  list-style: none;
  .laji {
    position: relative;
    img {
      border-radius: 10px;
    }
    span {
      position: absolute;
      bottom: 7px;
      right: 3px;
      color: white;
      font-size: 10px;
      background: rgba(0, 0, 0, 0.6);
      padding: 3px 6px;
      border-radius: 10px;
      i{
        display: inline-block;
        height: 10px;
        width: 12px;
        background-image: url("data:image/svg+xml;base64,");
        background-repeat: no-repeat;
        background-position:0 1px;
      }
    }
  }

  p {
    display: -webkit-box;
    font-size: 13px;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    margin: 0 5px 10px 5px;
  }
}
</style>