<template>
  <div id="app">
    <keep-alive>
      <router-view
        @nextprve="currentSongId = $event"
        @translate-currentid="asdasd($event)"
        @showchange="shows=$event"
        @musicstart="mcstart"
      ></router-view>
      <!-- <router-view></router-view> -->
    </keep-alive>
    <audio
      id="audio"
      ref="audio"
      v-if="currentSongId"
      :src="currentSongUrl"
      controls
      style="display:none"
      autoplay
    ></audio>
    <PlayMusic
      :musicid="currentSongId"
      @musicstart="mcstart"
      :onoff="button"
      :upmusic="shows"
      @showchange="shows=$event"
    ></PlayMusic>
  </div>
</template>
<script>
import PlayMusic from "@/components/PlayMusic.vue";
export default {
  components: {
    PlayMusic
  },
  name: "App",
  data() {
    return {
      currentSongId: null,
      button: true,
      shows: null,
      musiclist: null
    };
  },
  methods: {
    asdasd(event) {
      this.currentSongId = event[0];
      this.musiclist = event[1];
    },
    mcstart() {
      if (this.button == false) {
        this.$refs.audio.play();
        this.button = true;
      } else {
        this.$refs.audio.pause();
        this.button = false;
      }
    }
  },
  watch: {
    currentSongId: function() {
      this.button = true;
    }
  },
  computed: {
    currentSongUrl() {
      return (
        "https://music.163.com/song/media/outer/url?id=" +
        this.currentSongId +
        ".mp3"
      );
    }
  }
};
</script>
<style>
#app {
  height: 100%;
  overflow: hidden;
}
</style>
