<template>
  <div class="playinterfaces" v-if="musicdeta">
    <div class="back" @click="showmusic()"></div>
    <div class="playinterface">
      <img
        src="http://s3.music.126.net/mobile-new/img/needle-ip6.png?be4ebbe%E2%80%A6="
        :class="{styli:true,stylimg:$parent.button}"
      />
      <div class="film">
        <div ref="filmusic" class="filmmusic">
          <img class="filmimg" :src="musicdeta.al.picUrl" alt />
        </div>
        <div :class="{stoppause:!$parent.button}"></div>
      </div>

      <div class="lyrics">
        <h1 class="name">{{musicdeta.name}}</h1>
        <p class="lyric">
          <ul ref="lyricl">
            <li v-for="(item, index) in lyric" :key="index">{{item}}</li>
          </ul>
        </p>
      </div>
      <div class="nav">
        <!----- 滑动条 ------>
        <div class="progressbar">
          <div class="nowtime">{{nowtime}}</div>
          <div class="pro" ref="prowidth">
            <div class="circle" ref="circle"></div>
            <input
              ref="inputpro"
              type="range"
              class="range"
              value="0"
              max="1000"
              @input="inputchang"
            />
          </div>
          <div class="times">{{alltime}}</div>
        </div>
        <div class="navigationbar">
          <div class="prev" @click="fff(0)"></div>
          <div :class="{pause:true,pasuestop:$parent.button}" @click="pausechange()"></div>
          <div class="next" @click="fff(1)"></div>
        </div>
        <div>
          <div class="list" @click="listbutton=!listbutton"></div>
          <MusicList
            v-if="listbutton"
            :lists="$parent.musiclist"
            :name="musicdeta.name"
            @translate-currentid="xxxx($event)"
          ></MusicList>
        </div>
      </div>
    </div>
    <div class="active" :style="{'background-image':'url('+musicdeta.al.picUrl+')'}"></div>
  </div>
</template>

<script>
import MusicList from "@/components/MusicList.vue";
export default {
  components: {
    MusicList
  },
  data() {
    return {
      musicdeta: null,
      lyric: null,
      musiclist: null,
      listbutton: false,
      newid: null,
      nowtime: null,
      nowtimes: null,
      alltime: null,
      timershaft: null,
      iii:1
    };
  },
  watch: {
    newid: function() {
      this.getliist(this.newid);
    },
    nowtime: function() {
      if (
        parseInt(this.$parent.$refs.audio.currentTime % 60) < 10 &&
        parseInt(this.$parent.$refs.audio.currentTime / 60) < 10
      ) {
        this.nowtime =
          "0" +
          parseInt(this.$parent.$refs.audio.currentTime / 60) +
          " : 0" +
          parseInt(this.$parent.$refs.audio.currentTime % 60);
        this.nowtimes =
          "0" +
          parseInt(this.$parent.$refs.audio.currentTime / 60) +
          ":0" +
          parseInt(this.$parent.$refs.audio.currentTime % 60) +
          ".";
      } else if (
        parseInt(this.$parent.$refs.audio.currentTime % 60) < 10 &&
        parseInt(this.$parent.$refs.audio.currentTime / 60) >= 10
      ) {
        this.nowtime =
          parseInt(this.$parent.$refs.audio.currentTime / 60) +
          " : 0" +
          parseInt(this.$parent.$refs.audio.currentTime % 60);
        this.nowtimes =
          parseInt(this.$parent.$refs.audio.currentTime / 60) +
          ":0" +
          parseInt(this.$parent.$refs.audio.currentTime % 60) +
          ".";
      } else if (
        parseInt(this.$parent.$refs.audio.currentTime % 60) >= 10 &&
        parseInt(this.$parent.$refs.audio.currentTime / 60) < 10
      ) {
        this.nowtime =
          "0" +
          parseInt(this.$parent.$refs.audio.currentTime / 60) +
          " : " +
          parseInt(this.$parent.$refs.audio.currentTime % 60);
        this.nowtimes =
          "0" +
          parseInt(this.$parent.$refs.audio.currentTime / 60) +
          ":" +
          parseInt(this.$parent.$refs.audio.currentTime % 60) +
          ".";
      } else {
        this.nowtime =
          parseInt(this.$parent.$refs.audio.currentTime / 60) +
          " : " +
          parseInt(this.$parent.$refs.audio.currentTime % 60);
        this.nowtimes =
          parseInt(this.$parent.$refs.audio.currentTime / 60) +
          ":" +
          parseInt(this.$parent.$refs.audio.currentTime % 60) +
          ".";
      }

      if (
        parseInt(this.$parent.$refs.audio.duration % 60) < 10 &&
        parseInt(this.$parent.$refs.audio.duration / 60) < 10
      ) {
        this.alltime =
          "0" +
          parseInt(this.$parent.$refs.audio.duration / 60) +
          " : 0" +
          parseInt(this.$parent.$refs.audio.duration % 60);
      } else if (
        parseInt(this.$parent.$refs.audio.duration % 60) < 10 &&
        parseInt(this.$parent.$refs.audio.duration / 60) >= 10
      ) {
        this.alltime =
          parseInt(this.$parent.$refs.audio.duration / 60) +
          " : 0" +
          parseInt(this.$parent.$refs.audio.duration % 60);
      } else if (
        parseInt(this.$parent.$refs.audio.duration % 60) >= 10 &&
        parseInt(this.$parent.$refs.audio.duration / 60) < 10
      ) {
        this.alltime =
          "0" +
          parseInt(this.$parent.$refs.audio.duration / 60) +
          " : " +
          parseInt(this.$parent.$refs.audio.duration % 60);
      } else {
        this.alltime =
          parseInt(this.$parent.$refs.audio.duration / 60) +
          " : " +
          parseInt(this.$parent.$refs.audio.duration % 60);
      }

      if (this.timershaft) {
        for (let i = 0; i < this.timershaft.length; i++) {
          if (this.nowtimes == this.timershaft[i]) {
            this.$refs.lyricl.children[this.iii].style.color="rgba(255, 255, 255, 0.3)";
            this.iii=i+1;
            this.$refs.lyricl.children[i+1].style.color="#fff";
            this.$refs.lyricl.scrollTop=(i-2)*16.8;
          }
        }
      }
    }
  },
  methods: {
    inputchang() {
      this.$parent.$refs.audio.currentTime =
        (this.$refs.inputpro.valueAsNumber / 1000) *
        this.$parent.$refs.audio.duration;
    },
    timeup() {
      window.setInterval(() => {
        if (this.$parent.$refs.audio.duration) {
          this.$refs.inputpro.valueAsNumber =
            (this.$parent.$refs.audio.currentTime /
              this.$parent.$refs.audio.duration) *
            1000;
          this.$refs.circle.style.left =
            (this.$refs.inputpro.valueAsNumber / 1000) *
              (this.$refs.prowidth.clientWidth - 10) +
            "px";
          if (this.$refs.inputpro.valueAsNumber == 1000) {
            this.fff(1);
          }
          this.nowtime = this.$parent.$refs.audio.currentTime;
          this.alltime = this.$parent.$refs.audio.duration;
        }
      }, 100);
    },
    pausechange() {
      this.$emit("musicstart");
      if (!this.$parent.button) {
        this.$refs.filmusic.classList.add("filmimgstop");
      } else if (this.$refs.filmusic.classList.contains("filmimgstop")) {
        this.$refs.filmusic.classList.remove("filmimgstop");
      }
    },
    fff(x) {
      for (let i = 0; i < this.$parent.musiclist.length; i++) {
        if (
          this.$parent.currentSongId == this.$parent.musiclist[i].id &&
          x == 1
        ) {
          if (i + 1 == this.$parent.musiclist.length) {
            this.$emit("nextprve", this.$parent.musiclist[0].id);
            this.newid = this.$parent.musiclist[0].id;
            return;
          }
          this.$emit("nextprve", this.$parent.musiclist[i + 1].id);
          this.newid = this.$parent.musiclist[i + 1].id;
          return;
        } else if (
          this.$parent.currentSongId == this.$parent.musiclist[i].id &&
          x == 0
        ) {
          if (i - 1 < 0) {
            this.$emit(
              "nextprve",
              this.$parent.musiclist[this.$parent.musiclist.length - 1].id
            );
            this.newid = this.$parent.musiclist[
              this.$parent.musiclist.length - 1
            ].id;
            return;
          }
          this.$emit("nextprve", this.$parent.musiclist[i - 1].id);
          this.newid = this.$parent.musiclist[i - 1].id;
          return;
        }
      }
    },
    xxxx(event) {
      this.$emit("translate-currentid", [event, this.$parent.musiclist]);
      this.newid = event;
    },
    showmusic() {
      this.$router.go(-1);
      this.$emit("showchange", false);
    },
    getliist(id) {
      this.axios
        .get("https://music.kele8.cn/song/detail", {
          params: {
            ids: id
          }
        })
        .then(repson => {
          this.musicdeta = repson.data.songs[0];
        });
      this.axios
        .get("https://music.kele8.cn/lyric", {
          params: {
            id: id
          }
        })
        .then(repson => {
          var lyrics = repson.data.lrc.lyric;
          //this.lyric = lyrics.replace(/\[\d\d:\d\d\.\d\d\d\]/g, "");
          this.lyric=lyrics.split(/\[\d\d:\d\d\.\d\d\d\]/g);
          this.timershaft = lyrics.match(/\d\d:\d\d\./g);

          if (this.$refs.filmusic.classList.contains("aaaaa") == true) {
            this.$refs.filmusic.className = "filmmusic bbbbb";
          } else {
            this.$refs.filmusic.className = "filmmusic aaaaa";
            this.timeup();
          }
          if (!this.$parent.button) {
            this.$refs.filmusic.classList.add("filmimgstop");
          }
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getliist(to.query.id);
    });
  }
};
</script>

<style lang="less">
.playinterfaces {
  width: 100%;
  height: 100%;
  .back {
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    top: 10px;
    left: 15px;
    filter: blur(0px);
    transform: rotate(-135deg);
    background: url(../assets/back.png);
    background-size: cover;
    z-index: 5;
  }
  .playinterface {
    width: 100%;
    height: 100%;
    border-top: 1px solid rgba(0, 0, 0, 0);
    position: relative;

    .styli {
      width: 96px;
      height: 137px;
      position: absolute;
      left: calc(50% - 13px);
      top: -5px;
      z-index: 5;
      transition: transform 1s;
      transform-origin: 13% 0%;
      transform: rotate(-20deg);
    }
    .stylimg {
      transform: rotate(0deg);
    }
    .film {
      position: relative;
      margin: 65px auto 10px auto;
      width: 296px;
      height: 296px;
      display: flex;
      justify-content: center;
      align-items: center;
      .filmmusic {
        display: inline-block;
        width: 296px;
        height: 296px;
        background: url("http://s3.music.126.net/mobile-new/img/disc-ip6.png?6979612%E2%80%A6=");
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;
        .filmimg {
          position: absolute;
          width: 184px;
          height: 184px;
          border-radius: 50%;
        }
        &::after {
          content: "";
          display: inline-block;
          width: 296px;
          height: 296px;
          background: url("http://s3.music.126.net/mobile-new/img/disc_light.png?2bb24f3%E2%80%A6=");
          background-size: cover;
          position: absolute;
          z-index: 2;
        }
      }
      .bbbbb {
        animation: rotate 6s linear 0.5s infinite;
      }
      .aaaaa {
        animation: rotate1 6s linear 0.5s infinite;
      }
      .filmimgstop {
        animation-play-state: paused;
      }

      .stoppause {
        display: inline-block;
        width: 56px;
        height: 56px;
        background: url(data:image/png;base64,);
        background-size: cover;
        position: absolute;
        z-index: 2;
      }
    }
    .lyrics {
      text-align: center;
      color: rgb(240, 240, 240);
      h1.name {
        font-size: 18px;
        font-weight: 500;
        line-height: 40px;
      }
      p.lyric {
        width: 70%;
        height: 85px;
        line-height: 17px;
        margin: 10px auto 0 auto;
        font-size: 13px;
        overflow: hidden;
        white-space: pre-line;
        ul{
          height: 85px;
          overflow: hidden;
          li{
            color: rgba(255, 255, 255, 0.3)
          }
        }
      }
    }
    .nav {
      width: 100%;
      position: fixed;
      bottom: 0;
      .progressbar {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 10px;
        .nowtime {
          color: #fff;
          font-size: 10px;
          margin: 0 10px 0 25px;
        }
        .times {
          color: #fff;
          font-size: 10px;
          margin: 0 25px 0 10px;
        }
        .pro {
          flex: 1;
          border-top: 1px solid rgb(201, 201, 201);
          position: relative;
          .circle {
            position: absolute;
            top: -6px;
            width: 10px;
            height: 10px;
            background: #fff;
            border-radius: 50%;
          }
          .range {
            position: absolute;
            height: 0px;
            width: 100%;
            opacity: 0;
          }
        }
      }
      .navigationbar {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        margin: 20px 0;

        .prev {
          width: 30px;
          height: 30px;
          background: url(../assets/next.png);
          background-size: 30px;
          margin: 0 10px;
        }
        .next {
          width: 30px;
          height: 30px;
          background: url(../assets/next.png);
          background-size: 30px;
          transform: rotate(180deg);
          margin: 0 10px;
        }
        .pause {
          width: 40px;
          height: 40px;
          background-image: url(../assets/s.png);
          background-size: 40px;
          margin: 0 10px;
        }
        .pasuestop {
          background-image: url(../assets/p.png);
        }
      }
      .list {
        position: absolute;
        z-index: 5;
        bottom: 27px;
        right: 25px;
        width: 25px;
        height: 25px;
        background-image: url(../assets/list.png);
        background-size: 25px;
      }
    }
  }
  .active {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    filter: blur(11px);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1;
  }
  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    filter: blur(0px);
    background: rgba(0, 0, 0, 0.45);
    z-index: -1;
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotate1 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>