<template>
  <div class="lable">
    <div class="lableone">
      <span>标签：</span>
      <span class="lables">{{item.tags[0]}}</span>
      <span class="lables">{{item.tags[1]}}</span>
    </div>
    <div class="labletwo">
      <span style="float: left;">简介：</span>
      <span :class="{active: shows}">{{item.description}}</span>
    </div>
    <template v-if="getnum(item.description) > 2">
        <div :class="{button:true,xxx:!shows}" @click="shows=!shows"></div>
    </template>
    
  </div>
</template>

<script>
export default {
  props: ["item"],
  data() {
    return {
      shows: true
    };
  },
  methods: {
      getnum(s){
          return (s.split('\n')).length-1;
      }
  },
};
</script>

