<template>
  <div class="homesearch">
    <input
      id="inputsearch"
      type="text"
      placeholder="搜索歌曲、歌手、专辑"
      onKeypress="javascript:if(event.keyCode == 32)event.returnValue = false;"
      v-model="searchtext"
      autocomplete="off"
      @focus="xxx=true"
      @keyup.enter="xxx=false"
      @click="xxx=true"
    />
    <img
      src="data:image/svg+xml;base64,"
      class="imgsearch"
    />
    <img
      src="data:image/svg+xml;base64,"
      class="not"
      v-if="searchtext"
      @click="searchtext=''"
    />
    <hr />
    <h2 class="texthot" v-if="searchtext == undefined || searchtext == ''">热门搜索</h2>
    <ul class="hotsearch" v-if="searchname && (searchtext ==undefined || searchtext == '')">
      <li
        v-for="(item,index) in searchname.hots"
        :key="index"
        @click="changgetxxx(item.first)"
      >{{item.first}}</li>
    </ul>
    <SongSearch
      @translate-currentid="$emit('translate-currentid', $event)"
      :songname="searchtext"
      :xxx="xxx"
      @changexxx="changgetxxx"
    ></SongSearch>
  </div>
</template>

<script>
import SongSearch from "@/components/SongSearch.vue";
export default {
  components: {
    SongSearch
  },
  data() {
    return {
      searchname: null,
      searchtext: null,
      xxx: true
    };
  },
  methods: {
    changgetxxx(data) {
      this.xxx = false;
      this.searchtext = data;
    },
    gethotsearch() {
      this.axios.get("https://music.kele8.cn/search/hot").then(repson => {
        this.searchname = repson.data.result;
      });
    }
  },
  created() {
    this.gethotsearch();
  }
};
</script>

<style lang="less">
div.homesearch {
  padding: 15px 10px;
  position: relative;
  height: calc(100% - 179px);
  overflow: scroll;
  h2.texthot {
    font-size: 12px;
    color: rgb(105, 105, 105);
    margin-bottom: 5px;
  }
  .hotsearch {
    li {
      font-size: 14px;
      color: rgb(30, 30, 30);
      display: inline-block;
      border: 1px solid rgb(220, 220, 220);
      border-radius: 40px;
      padding: 5px 10px;
      margin: 5px;
    }
  }
  input {
    width: 100%;
    outline: none;
    border: none;
    background: rgb(235, 235, 235);
    padding: 8px 33px 7px 33px;
    border-radius: 40px;
    &::placeholder {
      color: rgb(190, 190, 190);
    }
  }
  img.imgsearch {
    width: 13px;
    height: 13px;
    position: absolute;
    top: 24px;
    left: 20px;
  }
  img.not {
    width: 13px;
    height: 13px;
    position: absolute;
    top: 24px;
    right: 20px;
  }
  hr {
    margin: 15px 0;
    border: 0;
    border-top: 1px solid rgb(240, 240, 240);
  }
}
</style>